@import url(https://fonts.googleapis.com/css2?family=Kaushan+Script&family=Merriweather:wght@300&display=swap);
body {
    background-image: linear-gradient(to bottom,#8e0e00 , #1f1c18);
    background-attachment: fixed;
    font-family: sans-serif;
}

::-webkit-scrollbar {
    width: 10px;
}

::-webkit-scrollbar-thumb {
    background-image: linear-gradient(to bottom,#8e0e00 , #1f1c18);
}

a {
    text-decoration: none;
    color: #354548;
}

a:hover {
    text-decoration: none;
    color: black;
}

.app__container {
    padding-top: 50px;
}

.app__row {
    height: 86vh;
}

.app__main {
    background: #fff;
    border-radius: 20px;
    padding: 25px 15px;
}

/* Sidebar */

.sidebar {
    background: #fff;
    height: 100%;
    border-radius: 20px;
    padding: 25px 15px;
    box-shadow: 0 0 10px white;
    text-align: center;
}

.sidebar__avatar {
    height: 150px;
    width: 150px;
    object-fit: cover;
}

.sidebar__icon {
    width: 25px;
    height: 25px;
}

.sidebar__name {
    font-family: 'Kaushan Script';
    margin: 20px 0;
    font-size: 1.7rem;
}

.sidebar__name span {
    color: #8e0e00;
}

.sidebar__item {
    margin-bottom: 6px;
    padding-top: 6px;
    height: 40px;
    font-size: 1.05rem;
    border-radius: 30px;
}

.sidebar__title {
    background-color: #f1f1f1;
    border-radius: 30px;
}

.sidebar__socialicons .sidebar__icon {
    margin: 3px;
}

.sidebar__contact {
    background-color: #dbdddf;
    padding: 2px 0;
    margin: 20px -15px;
}

.sidebar__email {
    background-image: linear-gradient(to bottom,#8e0e00 , #1f1c18);
    border-radius: 30px;
    color: #fff;
    margin: 15px 25px 0;
    cursor: pointer;
    transition: all 3s linear;
}

.sidebar__email:hover {
    transform: scale(1.2);
}

.sidebar__github:hover,
.sidebar__resume:hover {
    cursor: pointer;
    background-color: #f1f1f2;
    color: #000;
}

@media(max-width:991px) {
    .app__main {
        margin: 30px 0;
    }
}

/* Navbar */

.navbar__items {
    display: flex;
}

.navbar__active {
    font-size: 2rem;
    font-weight: 700;
    color: #354358;
    border-bottom: 4px solid #8e0e00;
}

.navbar__item {
    margin-right: 20px;
    text-decoration: none;
    color: #354358;
    font-size: 1.3rem;
    cursor: pointer;
}

.navbar__item:hover {
    background: -webkit-linear-gradient(#8e0e00 , #1f1c18);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

/* Skillcard */

.skillcard {
    background-color: #fff;
    margin: 10px;
    padding: 10px;
    display: flex;
    flex-direction: row;
    border-radius: 10px;
}

.skillcard__icon {
    width: 40px;
    height: 40px;
}

.skillcard__body {
    margin-left: 15px;
}

.skillcard__content {
    margin: 0;
    font-size: 0.9rem;
    color: #6e7e91;
}

/* Bar */

.bar {
    width: 90%;
    background-color: #f1f2f3;
    border-radius: 20px;
    margin-bottom: 12px;
}

.bar__icon {
    width: 15px;
    height: 15px;
}

.bar__wrapper {
    background-image: linear-gradient(90deg,#8e0e00 , #1f1c18);
    border-radius: 20px;
    padding: 4px 14px;
    color: #fff;
    font-size: 0.9rem;
}

/* RESPONSIVE CSS */

@media (max-width: 600px) {
    .app__main {
        margin-right: 10px;
        margin-left: 10px;
    }

    .navbar__active {
        font-size: 1.5rem;
    }

    .navbar__item {
        font-size: 1rem;
    }
}
/* About Section */

.about {
    padding: 20px;
    margin: -35px;
}

.about__intro {
    font-size: 1rem;
    padding: 20px 28px;
    margin: 10px 0;
}

.about__container {
    background-color: #dbdddf;
    padding: 30px 20px;
}

.about__heading {
    font-size: 25px;
    font-weight: 700;
    color: #354358;
    margin-left: 10px;
}

/* Resume */

.resume {
    padding-top: 20px;
}

.resume-card__heading,
.resume-language__heading {
    font-size: 1.6rem;
    font-weight: 700;
    margin-left: 10px;
    color: #354538;
} 

.resume-card__body {
    margin-top: 10px;
}

.resume-card__title {
    font-weight: 700;
}

.resume-card__name {
    font-size: 16px;
}

.resume-card__details {
    font-size: 0.9rem;
    padding-top: 20px;
}

/* Projects */

.projects {
    height: 70vh;
    overflow-y: auto;
}

.projectCard__wrapper {
    margin: 10px 0;
    width: 100%;
    height: 100%;
    min-height: 160px;
}

.projectCard__img {
    width: 100%;
    height: 80%;
    border-radius: 30px;
    border: 1px solid #8e0e00;
    transition: transform 0.5s linear;
}

.projectCard__img:hover {
    transform: scale(1.05);
}

.projectCard__icon {
    width: 25px;
    height: 25px;
}

.projectCard__title {
    margin-top: 12px;
    font-size: 1rem;
    display: flex;
    align-items: center;
    justify-content: center;
}

